
.sundance{
	.headline-1{
	  font-family: var(--font-sdtv-primary);
	  font-size: 3rem;
	  font-weight: 300;
	  text-transform: lowercase;
	  line-height: 1.08;
	}

	.headline-2{
	  font-family: var(--font-sdtv-primary);
	  font-size: 2.5rem;
	  font-weight: 200;
	  text-transform: lowercase;
	  line-height: get-line-height(3.25rem,3rem);
	}

	.headline-3{
	  font-family: var(--font-sdtv-primary);
	  font-size: 2rem;
	  font-weight: 200;
	  text-transform: lowercase;
	  line-height: get-line-height(2.25rem,2rem);
	}

	.headline-4{
	  font-family: var(--font-sdtv-primary);
	  font-size: 1.5rem;
	  font-weight: 300;
	  text-transform: lowercase;
	  line-height: get-line-height(1.75rem,1.5rem);
	}

	.headline-5{
	  font-family: var(--font-sdtv-primary);
	  font-size: 1.25rem;
	  text-transform: lowercase;
	  font-weight: 300;
	  line-height: get-line-height(1.75rem,1.25rem);
	}

	.headline-5--strong{
	  font-family: var(--font-sdtv-primary);
	  font-size: 1.25rem;
	  font-weight: 700;
	  text-transform: lowercase;
	  line-height: get-line-height(1.75rem,1.25rem);
	}

	.headline-6{
	  font-family: var(--font-sdtv-primary);
	  font-size: 1.125rem;
	  font-weight: 300;
	  text-transform: lowercase;
	  line-height: get-line-height(1.25rem,1.125rem);
	}

	.body-1{
	  font-family: var(--font-sdtv-primary);
	  font-size: 1rem;
	  font-weight: 400;
	  line-height: 1.5;
	  letter-spacing: normal;
	}

	.body-2{
	  font-family: var(--font-sdtv-primary);
	  font-size: .875rem;
	  font-weight: 400;
	  font-style: normal;
	  line-height: 1.43;
	  letter-spacing: normal;
	}

	.subhead-1{
	  font-family: var(--font-sdtv-primary);
	  font-size: 1rem;
	  font-weight: 300;
	  line-height: 1.25;
	  letter-spacing: normal;
	}

	.subhead-2{
	  font-family: var(--font-sdtv-primary);
	  font-size: .875rem;
	  font-weight: 300;
	  line-height: 1.14;
	  letter-spacing: normal;
	}

	.button{
	  font-family: var(--font-sdtv-primary);
	  font-size: 1rem;
	  text-transform: uppercase;
	  font-weight: 700;
	  line-height: 1.125;
	  letter-spacing: 1.25px;
	}

	.caption{
	  font-family: var(--font-sdtv-primary);
	  font-size: .875rem;
	  font-weight: 600;
	  text-transform: uppercase;
	  line-height: 1;
	  letter-spacing: 1px;
	}

	.label{
	  font-family: var(--font-sdtv-primary);
	  font-size: .75rem;
	  font-weight: 600;
	  text-transform: uppercase;
	  line-height: .875;
	  letter-spacing: 1.5px;
	}

	p{
	  font-family: var(--font-sdtv-primary);
	  font-weight: 300;
	  font-style: normal;
	  line-height: 1;
	  margin-top:0;
	  margin-bottom:1rem;
	}
}


@media screen and (min-width: 960px){
  .fs-desktop-15{font-size: var(--fs15);}
  .fs-desktop-14{font-size: var(--fs14);}
  .fs-desktop-13{font-size: var(--fs13);}
  .fs-desktop-12{font-size: var(--fs12);}
  .fs-desktop-11{font-size: var(--fs11);}
  .fs-desktop-10{font-size: var(--fs10);}
  .fs-desktop-09{font-size: var(--fs09);}
  .fs-desktop-08{font-size: var(--fs08);}
  .fs-desktop-07{font-size: var(--fs07);}
  .fs-desktop-06{font-size: var(--fs06);}
  .fs-desktop-05{font-size: var(--fs05);}
  .fs-desktop-04{font-size: var(--fs04);}
  .fs-desktop-03{font-size: var(--fs03);}
  .fs-desktop-02{font-size: var(--fs02);}
  .fs-desktop-01{font-size: var(--fs01);}
  .fs-desktop-00{font-size: var(--fs00);}
}